<template>
    <div>
        <u-button size="small" width="100px" height="30px" @on-click="onDownload">下载</u-button>
    </div>
</template>
<script>
    import img from './img/favor_button.png';
    export default {
        data () {
            return {
                btnImg: img
            };
        },
        methods: {
            onDownload () {
                this.$Core.App.loaders().load('http://zonemin.bs2cdn.yy.com/group16/M00/9464/20190311_1857303575180.xml', 'xml',
                    (url, data) => {
                        console.info(url, data);
                    });
            }
        }
    };
</script>
<style>
</style>



